@import url(https://fonts.googleapis.com/css2?family=Dancing+Script&family=Great+Vibes&family=Josefin+Slab:wght@100&family=Parisienne&display=swap);
body{
    margin: 0;
    padding: 0;
background: #09203f;
background: radial-gradient(circle, #09203f 0%, #537895 100%);
font-family: 'Dancing Script', cursive;
font-family: 'Great Vibes', cursive;
font-family: 'Parisienne', cursive;
font-family: 'Josefin Slab', serif;
}
* {
    box-sizing: border-box;
  }
  
.custom-button{min-width:150px;width:auto;height:40px;letter-spacing:.5px;margin-top:20px;font-size:15px;background-color:#607196;color:#fff;font-weight:bolder;border-radius:10px;border:none;cursor:pointer;display:-webkit-flex;display:flex;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}@media screen and (max-width: 750px){.custom-button{min-width:100px}}.custom-button:hover{background-color:#fff;color:#000;border:1px solid #000}.custom-button.google-sign-in{background-color:#4285f4;color:#fff}.custom-button.google-sign-in:hover{background-color:#fff;color:#000;border:1px solid #000}
.profilepage{margin-top:0px;padding-top:0px;margin-bottom:30px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:space-around;justify-content:space-around;-webkit-align-items:center;align-items:center}.img{border-radius:50%;border:5px solid #607196;width:150px;height:150px;margin-top:100px}.item-enter{opacity:0}.item-enter-active{opacity:1;transition:opacity 200ms ease-in}.item-exit{opacity:1}.item-exit-active{opacity:0;transition:opacity 200ms ease-in}.skillcard-container{height:100px;font-size:30px;display:grid;grid-template-columns:1fr 1fr 1fr;grid-column-gap:15px;-webkit-column-gap:15px;column-gap:15px;overflow:hidden}.media{margin:10px 0 10px 0;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:space-around;justify-content:space-around}.media .fa{color:snow;border-radius:50%;border:3px solid #607196;-webkit-text-decoration-line:none;text-decoration-line:none;padding:5px;margin:2px}.media .fa:hover{opacity:.7}
.skillcard{width:100px;background-color:snow;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center;font-size:15px;border-radius:5px;height:100px;color:#000;font-weight:bold}.skillcard .fa{font-size:35px;display:block;margin-bottom:15px}.skillcard .dots{display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;margin-top:5px}.skillcard .dots .dot{height:10px;width:10px;background-color:green;border-radius:50%;display:inline-block}
.display-text{width:100%;color:snow;margin:30px 0 30px 0;font-size:25px;display:-webkit-flex;display:flex;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center}@media screen and (max-width: 750px){.display-text{font-size:18px}}
.project-menu{width:80%;font-size:15px;color:#000;display:grid;grid-template-columns:1fr 1fr 1fr;grid-column-gap:5px;-webkit-column-gap:5px;column-gap:5px}@media screen and (max-width: 750px){.project-menu{grid-template-columns:1fr 1fr;margin-bottom:20px}}.project-menu .item-card{border:1px solid #000;border-radius:10px;padding:10px}.project-menu .item-card:hover{-webkit-transform:scale(1.1);transform:scale(1.1)}
.item-card{background-color:snow;height:240px;margin-top:50px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column}.item-card .menu-item{min-width:35%;height:240px;margin:50px;-webkit-flex:auto;flex:auto;background-color:#607196;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-align-items:center;align-items:center;-webkit-justify-content:center;justify-content:center;border:1px solid #607196;margin:0 7.5px 15px;background-position:center;background-size:cover;overflow:hidden;position:relative}.item-card .menu-item:hover{cursor:pointer}.item-card .menu-item:hover .background-image{-webkit-transform:scale(1.1);transform:scale(1.1);transition:-webkit-transform 5s cubic-bezier(0.25, 0.45, 0.45, 0.95);transition:transform 5s cubic-bezier(0.25, 0.45, 0.45, 0.95);transition:transform 5s cubic-bezier(0.25, 0.45, 0.45, 0.95), -webkit-transform 5s cubic-bezier(0.25, 0.45, 0.45, 0.95)}.item-card .menu-item:hover .content{opacity:.8}.item-card .menu-item.large{height:400px}.item-card .menu-item:first-child{margin-right:7.5px}.item-card .menu-item:last-child{margin-right:7.5px}.item-card .menu-item .background-image{position:absolute;margin:0px;padding:0px;width:100%;height:100%;background-size:cover;background-position:center}.item-card .content{height:90px;padding:10px;font-size:10px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:flex-start;align-items:flex-start;-webkit-justify-content:space-around;justify-content:space-around;color:#000;background-color:snow;text-decoration:none}.item-card .content .title{text-decoration:none;font-weight:bolder;margin-bottom:5px;font-size:1rem;color:#000}.item-card .content .subtitle{font-weight:bold;color:#000;font-size:.8rem;margin-bottom:5px}
.header{width:85%;margin-top:10px;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:space-between;justify-content:space-between;-webkit-align-items:center;align-items:center}.header .fa{color:rgba(117,43,16,.829)}.header .fa:hover{color:#000}.header .menu-bar{background:none;border:none;font-size:25px;cursor:pointer;display:none}.header .nameIcon{color:#fff;font-size:30px;margin:10px;font-family:"Dancing Script",cursive}.header .download-button{border-radius:20px;text-decoration:none;margin:10px;height:30px;display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly}@media screen and (max-width: 750px){.header .download-button{margin:10px 0px;font-size:10px;display:-webkit-flex;display:flex;-webkit-justify-content:space-evenly;justify-content:space-evenly}}.header .menu-item{height:30px;width:80%;background-color:#607196;border-radius:20px;font-weight:bold;padding:5px;overflow:hidden;display:-webkit-flex;display:flex;-webkit-flex-direction:row;flex-direction:row;-webkit-justify-content:flex-end;justify-content:flex-end}.header .menu-item .option{float:center;display:block;margin:0px 20px;text-align:center;text-decoration:none;color:#fff}@media screen and (max-width: 750px){.header .menu-item .option{margin:0px 10px}}.header .menu-item .option:hover{height:2px;color:#000;opacity:.8}@media screen and (max-width: 750px){.header .option{margin:15px 10px}.header .menu-bar{display:none}.header .menu-bar:hover{color:snow}}
.sign-in{width:400px;height:400px;border-radius:10px;box-shadow:1px 5px 5px 5px snow;margin-top:60px;color:#000;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-justify-content:center;justify-content:center;-webkit-align-items:center;align-items:center}@media screen and (max-width: 750px){.sign-in{width:200px;height:300px;font-size:15px;box-shadow:1px 3px 3px 3px snow}}.sign-in .input{margin-top:0px;padding:0px;display:-webkit-flex;display:flex;-webkit-flex-direction:column;flex-direction:column;-webkit-align-items:center;align-items:center}

