.footer{width:100%;height:20px;text-align:right;color:snow;position:fixed;bottom:0;display:-webkit-flex;display:flex;margin-top:30px;font-size:.6rem;display:flex;-webkit-align-items:flex-end;align-items:flex-end;-webkit-justify-content:flex-end;justify-content:flex-end}
